$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.currency-checker-tabs {
	display: flex;
	list-style: none;
	padding: 0;
	margin: 0;
	border-radius: 5px;
	overflow: hidden;

	&__item {
		flex-grow: 1;
	}

	&__item + &__item {
		& > * {
			margin-left: -1px;
		}

		label {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
	}

	&__item:not(:last-child) {
		label {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}
	}
}
