$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.currency-viewer {
	white-space: nowrap;

	&:after {
		font-weight: 400;
	}

	&[data-type="RUB"] {
		&:after {
			content: " ₽";
		}
	}

	&[data-type="EUR"] {
		&:before {
			content: "€";
		}
	}

	&[data-type="USD"] {
		&:before {
			content: "$";
		}
	}
}