$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.page {
	min-height: 100vh;
	transition: background-color 0.2s ease-in-out;

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 5rem 1.5rem;
	}

	&__logo {
		margin-bottom: 5rem;
	}

	&__content {
		width: 100%;
	}

	&.theme--night-mode {
		background: #05263f;
	}
}

.layout {
	max-width: 820px;
	margin: 0 auto;

	&__toolbar {
		margin-bottom: 2rem;
	}

	&__content {
		display: flex;
	}

	&__sidebar {
		width: 232px;
		flex-grow: 0;
		flex-shrink: 0;
	}

	&__body {
		flex-grow: 1;
	}

	&__sidebar + &__body {
		margin-left: 2rem;
	}
}
