$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.filter-currency-checker-list {
	padding-right: 1.6rem;
	padding-left: 1.6rem;

	&__row {
		padding: 0;
	}

	&__row + &__row {
		margin-top: .8rem;
	}
}