$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.checkbox-line {
	display: flex;
	align-items: center;
	position: relative;
	overflow: hidden;
	transition: all 0.2s linear;
	padding-right: 1.6rem;
	padding-left: 1.6rem;

	&__checkbox {
		padding: 0.8rem 1.6rem;
	}

	&__checkbox-wrapper {
		display: flex;
		font-size: 1.3rem;
		flex-grow: 1;

		.checkbox__label {
			//white-space: nowrap;
		}

		.checkbox {
			width: 100%;
		}
	}

	&__action-wrapper {
		opacity: 0;
		transition: all 0.2s linear;
		transform: translateY(50%);
		padding-left: 1rem;
		font-size: 1.1rem;
	}

	&__action {
		text-transform: uppercase;
		color: #2196F3;
		font-weight: 500;
		text-decoration: none;
	}

	&:hover {
		background: #F1FCFF;

		.checkbox-line__action-wrapper {
			opacity: 1;
			transform: translateY(0%);
		}
	}
}

.theme--night-mode {
	.checkbox-line:hover {
		background-color: rgba(255, 255, 255, 0.1);
	}

	.checkbox-line__action {
		color: #fff;
	}
}