$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.filter-section-wrapper {
	padding: 0;
	margin: 0;

	&__title {
		margin-bottom: 1.2rem;
		text-transform: uppercase;
		font-weight: 500;
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}

	&__body {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	&__body.skeleton {
		padding: 0 1.5rem;
	}
}

.theme--night-mode .filter-section-wrapper {
	color: #fff;
}