$border-radius: 5px;
$shadow: rgba(91, 137, 164, .25) 0 1px 4px;
@mixin styled-box($br: $border-radius, $sh: $shadow) {
	border-radius: $br;
	box-shadow: $sh;
}

.flight-date {
	&__time {
		color: #4A4A4A;
		font-size: 3.2rem;
		margin-bottom: 0.3125em;
		line-height: 1;
		display: inline-block;
	}

	&__place {
		color: #4A4A4A;
		font-weight: 500;
		font-size: 1.2rem;
		margin-bottom: 0.5em;
	}

	&__date {
		font-size: 1.2rem;
		color: #8B9497;
	}

	&--to {
		text-align: right;
	}

	&--from {
		text-align: left;
	}
}

.theme--night-mode {
	.flight-date__time,
	.flight-date__place,
	.flight-date__date {
		color: #fff;
	}
}
